.nav-item a:hover ~ .subrayado::before {
  width: 100%;
}

.subrayado {
  position: relative;
  display: block;
  width: 50%;
}

.subrayado::before {
  content: "";
  height: 2px;
  width: 0%;
  bottom: 0;
  position: absolute;
  background: linear-gradient(to right, #b06ab3, #2196f3);
  transition: 0.3s ease width;
  left: 50%;
}

.switch {
  position: relative;
  cursor: pointer;
  width: 40px;
  height: 40px;
  background: linear-gradient(45deg, rgb(202, 19, 214) 20%, rgb(0, 212, 255) 80%);
  box-shadow: 0 5px 25px rgba(0, 0, 0, 0.363);
  border-radius: 5px;
}
.switch input {
  opacity: 0;
  width: 100%;
  height: 100%;
  z-index: 1;
  cursor: pointer;
  position: absolute;
}
.switch div {
  width: 80%;
  height: 80%;
  margin: 10%;
  position: absolute;
}
.switch div span {
  background: #fff;
  width: 100%;
  height: 2px;
  border-radius: 5px;
  display: block;
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  transition: transform 0.5s, width 0.5s;
}
.switch div .line-1 {
  width: 50%;
  transform: translate(-100%, -10px);
  transform-origin: 0 0;
}
.switch div .line-3 {
  width: 50%;
  left: auto;
  right: 0;
  transform: translate(0, 10px);
  transform-origin: 100% 0;
}
.switch input:checked + div span.line-1 {
  transform: translate(0, 0) rotate(-45deg);
}
.switch input:checked + div span.line-3 {
  transform: translate(-100%, 0) rotate(-45deg);
}
.switch input:checked + div span.line-2 {
  transform: translate(-50%, -50%) rotate(45deg);
}
@media (min-width: 768px) {
  .switch {
    display: none;
  }
}

.mostrar {
  visibility: visible;
  opacity: 1;
  height: auto;
}

.checkbox-wrapper-5 {
  line-height: 0;
  display: flex;
  justify-content: center;
  padding: 1rem;
}
.checkbox-wrapper-5 .check {
  --size: 28px;
  position: relative;
  background: linear-gradient(90deg, #f19af3, #f099b5);
  line-height: 0;
  perspective: 400px;
  font-size: var(--size);
}
.checkbox-wrapper-5 .check input[type=checkbox],
.checkbox-wrapper-5 .check label,
.checkbox-wrapper-5 .check label::before,
.checkbox-wrapper-5 .check label::after, .checkbox-wrapper-5 .check {
  appearance: none;
  display: inline-block;
  border-radius: var(--size);
  border: 0;
  transition: 0.35s ease-in-out;
  box-sizing: border-box;
  cursor: pointer;
}
.checkbox-wrapper-5 .check label {
  width: calc(2.2 * var(--size));
  height: var(--size);
  background: #d7d7d7;
  overflow: hidden;
}
.checkbox-wrapper-5 .check input[type=checkbox] {
  position: absolute;
  z-index: 1;
  width: calc(0.8 * var(--size));
  height: calc(0.8 * var(--size));
  top: calc(0.1 * var(--size));
  left: calc(0.1 * var(--size));
  background: linear-gradient(45deg, #dedede, #ffffff);
  box-shadow: 0 6px 7px rgba(0, 0, 0, 0.3);
  outline: none;
  margin: 0;
}
.checkbox-wrapper-5 .check input[type=checkbox]:checked {
  left: calc(1.3 * var(--size));
}
.checkbox-wrapper-5 .check input[type=checkbox]:checked + label {
  background: transparent;
}
.checkbox-wrapper-5 .check label::before,
.checkbox-wrapper-5 .check label::after {
  content: "· ·";
  position: absolute;
  overflow: hidden;
  left: calc(0.15 * var(--size));
  top: calc(0.5 * var(--size));
  height: var(--size);
  letter-spacing: calc(-0.04 * var(--size));
  color: #9b9b9b;
  font-family: "Times New Roman", serif;
  z-index: 2;
  font-size: calc(0.6 * var(--size));
  border-radius: 0;
  transform-origin: 0 0 calc(-0.5 * var(--size));
  backface-visibility: hidden;
}
.checkbox-wrapper-5 .check label::after {
  content: "●";
  top: calc(0.65 * var(--size));
  left: calc(0.2 * var(--size));
  height: calc(0.1 * var(--size));
  width: calc(0.35 * var(--size));
  font-size: calc(0.2 * var(--size));
  transform-origin: 0 0 calc(-0.4 * var(--size));
}
.checkbox-wrapper-5 .check input[type=checkbox]:checked + label::before,
.checkbox-wrapper-5 .check input[type=checkbox]:checked + label::after {
  left: calc(1.55 * var(--size));
  top: calc(0.4 * var(--size));
  line-height: calc(0.1 * var(--size));
  transform: rotateY(360deg);
}
.checkbox-wrapper-5 .check input[type=checkbox]:checked + label::after {
  height: calc(0.16 * var(--size));
  top: calc(0.55 * var(--size));
  left: calc(1.6 * var(--size));
  font-size: calc(0.6 * var(--size));
  line-height: 0;
}

