.contenedor-portfolios {
  background-color: rgba(255, 255, 255, 0.3);
}
.contenedor-portfolios h4 {
  font-size: 4rem;
  margin: 2rem;
  color: #6A0888;
  text-align: center;
}

.portfolios {
  display: grid;
  place-items: center;
}
@media (min-width: 768px) {
  .portfolios {
    display: grid;
    gap: 1fr;
    grid-template-columns: repeat(3, 1fr);
  }
}
@media (min-width: 1024px) {
  .portfolios {
    display: grid;
    place-items: center;
    grid-template-columns: repeat(4, 2fr);
  }
  .portfolios .card-grid-1 {
    grid-column: 2/3;
  }
}

/** Estilos de Card **/
.card {
  width: 240px;
  height: 304px;
  background-image: linear-gradient(163deg, #fe2ef7 0%, #2efef7 100%);
  border-radius: 20px;
  transition: all 0.3s;
  margin: 2rem;
}

.contenedor-card {
  width: 240px;
  height: 304px;
  background-color: #1a1a1a;
  border-radius: 10px;
  transition: all 0.2s;
  padding: 1rem;
}
.contenedor-card h4,
.contenedor-card p {
  margin: 0;
}
.contenedor-card h4 {
  font-size: 1.8rem;
  color: #FFFFFF;
  font-weight: 700;
}
.contenedor-card p {
  font-size: 1.5rem;
  color: #e1e1e1;
}
.contenedor-card a {
  color: #2EFEF7;
  font-weight: 700;
}

.contenedor-card:hover {
  transform: scale(0.98);
  border-radius: 20px;
}

.card:hover {
  box-shadow: 0px 0px 30px 1px rgba(0, 255, 117, 0.3);
}

@media (min-width: 768px) {
  .contenedor-freelance {
    display: grid;
    grid-template-columns: 1.3fr 1.3fr;
    gap: 2rem;
    padding: 5rem 8rem;
  }
  .contenedor-freelance .disponible {
    display: flex;
    flex-direction: column;
    justify-content: baseline;
  }
}
.freelance h3,
.freelance h4,
.freelance p {
  margin: 0;
  text-align: center;
}
.freelance .disponible {
  padding: 2rem;
}
.freelance .disponible h3 {
  color: #000000;
  font-size: 4rem;
  font-weight: 700;
  line-height: 4rem;
}
.freelance .disponible p {
  color: #333333;
  margin: 2.5rem 3rem;
}
.freelance .disponible .letra-disponible {
  animation: agrandar 2s infinite;
  position: relative;
}
@keyframes agrandar {
  0% {
    transform: scale(1);
    color: #0e7a57;
  }
  50% {
    transform: scale(1.05);
    color: rgb(24, 214, 152);
  }
  100% {
    transform: scale(1);
    color: #0e7a57;
  }
}
.freelance .contactame {
  padding: 2rem;
}
.freelance .contactame h3 {
  color: #000000;
  font-weight: 500;
  font-size: 2.5rem;
}
.freelance .contactame h4 {
  color: #2EFEF7;
  text-shadow: 4px 4px 15px #000000;
  font-weight: 700;
  font-size: 5rem;
}
.freelance .contactame .iconos-contacto {
  display: flex;
  flex-direction: row;
  justify-content: space-evenly;
  margin: 2rem;
}
.freelance .contactame .iconos-contacto .icono-1,
.freelance .contactame .iconos-contacto .icono-2 {
  width: 75px;
  margin: 10px;
}
.freelance .contactame .iconos-contacto .icono-3 {
  width: 95px;
}